import React, { Component } from 'react'
import './style/horror.css';
import HorrorGameSlide from './horror.game.slide';
import { Card } from "react-bootstrap";
import axios from "axios";
import { Link } from "react-router-dom";
export default class horrorgame extends Component {
    constructor(props) {
        super(props);
        this.state = {
          horrorgames: [],
        };
      }
    
      async componentDidMount() {
        await axios
          .get("https://gameweb-react-app.herokuapp.com/horrorgames/")
          .then((res) => {
            this.setState({ horrorgames: res.data });
          })
          .catch((err) => {
            console.log(err);
          });
      }
    render() {
        return (
            <div>
            <HorrorGameSlide />
            <div className="bg-secondary">
        <br></br>
        <div className="container">
          <div className="row ">
            {this.state.horrorgames.map((horrgames) => (
              <div className="col-md-4 col-sd-4 col-lg-4 p-2" key={horrgames._id}>
              <Card style={{height:"23rem" }} className="bg-dark">
                
                <Card.Img variant="top" src={horrgames.imgurl} style={{height:"14rem"}} />
                <Card.Body>
                <div className="d-flex justify-content-start">
                <Card.Title style={{color:"white", }}>{horrgames.gamename}</Card.Title>
                  </div>
                  
                  <div className="d-flex justify-content-center" style={{marginBottom:"5px", marginTop:"10px"}}>
                  <Link
                    to={"/gamedetails/"+horrgames._id}
                    className="btn btn-success btn-block"
                    
                  >
                    Download
                  </Link>
                  </div>
                  
                </Card.Body>
              </Card>
            </div>
            ))}
          </div>
        </div>
        </div>
            </div>
        )
    }
}
