import React, { Component } from "react";
import "./style/adventure.css";
import AdvantureGameSlide from "./advanture.game.slide";
import { Card } from "react-bootstrap";
import axios from "axios";
import { Link } from "react-router-dom";
export default class advanturegame extends Component {
  constructor(props) {
    super(props);
    this.state = {
      advanturegames: [],
    };
  }

  async componentDidMount() {
    await axios
      .get("https://gameweb-react-app.herokuapp.com/advanturegames/")
      .then((res) => {
        this.setState({ advanturegames: res.data });
      })
      .catch((err) => {
        console.log(err);
      });
  }
  render() {
    return (
      <div>
        <AdvantureGameSlide />
        <div className="bg-secondary">
          <br></br>
          <div className="container">
            <div className="row ">
              {this.state.advanturegames.map((advgames) => (
                <div className="col-md-4 col-sd-4 col-lg-4 p-2" key={advgames._id}>
                <Card style={{height:"23rem" }} className="bg-dark">
                  
                  <Card.Img variant="top" src={advgames.imgurl} style={{height:"14rem"}} />
                  <Card.Body>
                  <div className="d-flex justify-content-start">
                  <Card.Title style={{color:"white", }}>{advgames.gamename}</Card.Title>
                    </div>
                    
                    <div className="d-flex justify-content-center" style={{marginBottom:"5px", marginTop:"10px"}}>
                    <Link
                      to={"/gamedetails/"+advgames._id}
                      className="btn btn-success btn-block"
                      
                    >
                      Download
                    </Link>
                    </div>
                    
                  </Card.Body>
                </Card>
              </div>
              ))}
            </div>
          </div>
        </div>
      </div>
    );
  }
}
