import React, { Component } from "react";
import "./style/action.css";
import Carousel from "react-bootstrap/Carousel";
export default class RacingGameSlide extends Component {
  render() {
    return (
      <Carousel className="carousel">
        <Carousel.Item interval={1000}>
          <img
            className="d-block w-100 slideimg"
            src="https://images.alphacoders.com/717/717886.jpg"
            alt="First slide"
          />
          <Carousel.Caption>
            <h3>Need for Speed Payback</h3>
            <p>
              Need for Speed is a racing video game franchise published by
              Electronic Arts and currently developed by Criterion Games, the
              developers of Burnout.
            </p>
          </Carousel.Caption>
        </Carousel.Item>
        <Carousel.Item interval={500}>
          <img
            className="d-block w-100 slideimg"
            src="https://images7.alphacoders.com/686/686337.jpg"
            alt="second slide"
          />
          <Carousel.Caption>
            <h3>Dirt Rally</h3>
            <p>
              Dirt Rally is a racing video game developed and published by
              Codemasters for Microsoft Windows, and is part of the Dirt
              franchise.
            </p>
          </Carousel.Caption>
        </Carousel.Item>
        <Carousel.Item interval={500}>
          <img
            className="d-block w-100 slideimg"
            src="https://images4.alphacoders.com/709/709880.jpg"
            alt="Third slide"
          />
          <Carousel.Caption>
            <h3>Forza Horizon 3</h3>
            <p>
              Forza Horizon 3 is a 2016 racing video game developed by
              Playground Games and published by Microsoft Studios for Xbox One
              and Microsoft Windows.
            </p>
          </Carousel.Caption>
        </Carousel.Item>
        <Carousel.Item>
          <img
            className="d-block w-100 slideimg"
            src="https://images7.alphacoders.com/482/482949.jpg"
            alt="Forth slide"
          />
          <Carousel.Caption>
            <h3>The Crew</h3>
            <p>
              The Crew is an online-only racing video game developed by Ivory
              Tower and Ubisoft Reflections and published by Ubisoft
            </p>
          </Carousel.Caption>
        </Carousel.Item>
      </Carousel>
    );
  }
}
