import React, { Component } from "react";
import { Link } from "react-router-dom";
import "./style/nav.css";
import { Nav, Navbar } from "react-bootstrap";
export default class Navbars extends Component {
  render() {
    return (
      <Navbar collapseOnSelect expand="lg" bg="dark" variant="dark" fixed="top" >
        <Link to="/" className="nav-link brandname ">
          GAME POOL
        </Link>
        <Navbar.Toggle aria-controls="responsive-navbar-nav" />
        <Navbar.Collapse id="responsive-navbar-nav">
          <Nav className=" mr-auto navlist">
            
              <Link to="/" className="nav-link active navitems">
                Action
              </Link>
            
           
              <Link to="/racing" className="nav-link navitems">
                Racing
              </Link>
            
            
              <Link to="/sports" className="nav-link navitems">
                Sports
              </Link>
           
              <Link to="/advanture" className="nav-link navitems">
                Advanture
              </Link>
            
              <Link to="/horror" className="nav-link navitems">
                Horror
              </Link>
            
          </Nav>
        </Navbar.Collapse>
      </Navbar>
    );
  }
}
