import React from 'react';
import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import {HashRouter as Router, Route} from "react-router-dom";

import Navbars from "./frontend/navigation";
import actiongame from './frontend/action.game';
import racinggame from './frontend/racing.game';
import sportsgame from './frontend/sports.game';
import advanturegame from './frontend/advanture.game';
import horrorgame from './frontend/horror.game';
import Admin from './frontend/admin';
import GamesDetails from './frontend/games.details';

function App() {
  return (
    <Router>
      <Navbars />
      
      <Route path="/" exact component = {actiongame} />
      <Route path="/racing" component = {racinggame} />
      <Route path="/sports" component = {sportsgame} />
      <Route path="/advanture" component = {advanturegame} />
      <Route path="/horror" component = {horrorgame} />
      <Route path="/admin3395" component={Admin} />
      <Route path="/gamedetails/:id" component={GamesDetails} />

    </Router>
  );
}

export default App;
