import React, { Component } from "react";
import "./style/action.css";
import { Card } from "react-bootstrap";
import axios from "axios";
import { Link } from "react-router-dom";
import ActionGameSlide from "./action.game.slide";


export default class actiongame extends Component {
  constructor(props) {
    super(props);
    this.state = {
      actiongames: [],
    };
  }

  async componentDidMount() {
    await axios
      .get("https://gameweb-react-app.herokuapp.com/actiongames/")
      .then((res) => {
        this.setState({ actiongames: res.data });
      })
      .catch((err) => {
        console.log(err);
      });
  }
  render() {
    console.log(this.state.actiongames);
    return (
      <div>
        <ActionGameSlide />
        <div className="bg-secondary">
        <br></br>
        <div className="container">
          <div className="row ">
            {this.state.actiongames.map((actgames) => (
              <div className="col-md-4 col-sd-4 col-lg-4 p-2" key={actgames._id}>
                <Card style={{height:"23rem" }} className="bg-dark">
                  
                  <Card.Img variant="top" src={actgames.imgurl} style={{height:"14rem"}} />
                  <Card.Body>
                  <div className="d-flex justify-content-start">
                  <Card.Title style={{color:"white", }}>{actgames.gamename}</Card.Title>
                    </div>
                    
                    <div className="d-flex justify-content-center" style={{marginBottom:"5px", marginTop:"10px"}}>
                    <Link
                      to={"/gamedetails/"+actgames._id}
                      className="btn btn-success btn-block"
                      
                    >
                      Download
                    </Link>
                    </div>
                    
                  </Card.Body>
                </Card>
              </div>
            ))}
          </div>
        </div>
        </div>
        
      </div>
    );
  }
}
