import React, { Component } from "react";
import "./style/action.css";
import Carousel from "react-bootstrap/Carousel";
export default class SportsGameSlide extends Component {
  render() {
    return (
      <Carousel className="carousel">
        <Carousel.Item interval={1000}>
          <img
            className="d-block w-100 slideimg"
            src="https://images4.alphacoders.com/722/722624.jpg"
            alt="First slide"
          />
          <Carousel.Caption>
            <h3>FIFA 17</h3>
            <p>
              FIFA 17 is a sports video game in the FIFA series developed and
              published by Electronic Arts
            </p>
          </Carousel.Caption>
        </Carousel.Item>
        <Carousel.Item interval={500}>
          <img
            className="d-block w-100 slideimg"
            src="https://images8.alphacoders.com/866/866536.png"
            alt="second slide"
          />
          <Carousel.Caption>
            <h3>Pro Evolution Soccer 2015</h3>
            <p>
              Pro Evolution Soccer 2015 is a football simulation game developed
              by PES Productions and published by Konami
            </p>
          </Carousel.Caption>
        </Carousel.Item>
        <Carousel.Item interval={500}>
          <img
            className="d-block w-100 slideimg"
            src="https://images2.alphacoders.com/614/614172.jpg"
            alt="Third slide"
          />
          <Carousel.Caption>
            <h3>FIFA 16</h3>
            <p>
              FIFA 16 is an association football simulation video game developed
              by EA Canada and published by EA Sports
            </p>
          </Carousel.Caption>
        </Carousel.Item>
        <Carousel.Item>
          <img
            className="d-block w-100 slideimg"
            src="https://images5.alphacoders.com/613/613108.jpg"
            alt="Forth slide"
          />
          <Carousel.Caption>
            <h3>Madden NFL 16</h3>
            <p>
              Madden NFL 16 is an American football sports video game based on
              the National Football League and published by EA Sports
            </p>
          </Carousel.Caption>
        </Carousel.Item>
      </Carousel>
    );
  }
}
