import React, { Component } from "react";
import "./style/sports.css";
import SportsGameSlide from "./sports.game.slide";
import { Card } from "react-bootstrap";
import axios from "axios";
import { Link } from "react-router-dom";
export default class sportsgame extends Component {
  constructor(props) {
    super(props);
    this.state = {
      sportsgames: [],
    };
  }

  async componentDidMount() {
    await axios
      .get("https://gameweb-react-app.herokuapp.com/sportsgames/")
      .then((res) => {
        this.setState({ sportsgames: res.data });
      })
      .catch((err) => {
        console.log(err);
      });
  }
  render() {
    return (
      <div>
        <SportsGameSlide />
        <div className="bg-secondary">
        <br></br>
        <div className="container">
          <div className="row ">
            {this.state.sportsgames.map((sprgames) => (
              <div className="col-md-4 col-sd-4 col-lg-4 p-2" key={sprgames._id}>
              <Card style={{height:"23rem" }} className="bg-dark">
                
                <Card.Img variant="top" src={sprgames.imgurl} style={{height:"14rem"}} />
                <Card.Body>
                <div className="d-flex justify-content-start">
                <Card.Title style={{color:"white", }}>{sprgames.gamename}</Card.Title>
                  </div>
                  
                  <div className="d-flex justify-content-center" style={{marginBottom:"5px", marginTop:"10px"}}>
                  <Link
                    to={"/gamedetails/"+sprgames._id}
                    className="btn btn-success btn-block"
                    
                  >
                    Download
                  </Link>
                  </div>
                  
                </Card.Body>
              </Card>
            </div>
            ))}
          </div>
        </div>
        </div>
      </div>
    );
  }
}
