import React, { Component } from "react";
import "./style/action.css";
import Carousel from "react-bootstrap/Carousel";
export default class ActionGameSlide extends Component {
  render() {
    return (
      <Carousel className="carousel">
        <Carousel.Item interval={1000}>
          <img
            className="d-block w-100 slideimg"
            src="https://images.alphacoders.com/842/842583.jpg"
            alt="First slide"
          />
          <Carousel.Caption>
            <h3>Assassin's Creed Origins</h3>
            <p>
              Assassin's Creed Origins is an action-adventure video game
              developed by Ubisoft Montreal and published by Ubisoft.
            </p>
          </Carousel.Caption>
        </Carousel.Item>
        <Carousel.Item interval={500}>
          <img
            className="d-block w-100 slideimg"
            src="https://images8.alphacoders.com/617/617636.jpg"
            alt="second slide"
          />
          <Carousel.Caption>
            <h3>Call of Duty: Black Ops III</h3>
            <p>
              Call of Duty: Black Ops III is a 2015 first-person shooter video
              game, developed by Treyarch and published by Activision.
            </p>
          </Carousel.Caption>
        </Carousel.Item>
        <Carousel.Item interval={500}>
          <img
            className="d-block w-100 slideimg"
            src="https://images2.alphacoders.com/434/434500.jpg"
            alt="Third slide"
          />
          <Carousel.Caption>
            <h3>Battlefield 4</h3>
            <p>
              Battlefield 4 is a first-person shooter video game developed by
              video game developer EA DICE and published by Electronic Arts.
            </p>
          </Carousel.Caption>
        </Carousel.Item>
        <Carousel.Item>
          <img
            className="d-block w-100 slideimg"
            src="https://images.alphacoders.com/263/263441.jpg"
            alt="Forth slide"
          />
          <Carousel.Caption>
            <h3>Crysis 3</h3>
            <p>
              Crysis 3 is a first-person shooter video game developed by Crytek
              and published in 2013 by Electronic Arts
            </p>
          </Carousel.Caption>
        </Carousel.Item>
      </Carousel>
    );
  }
}
