import React, { Component } from 'react'
import "./style/action.css";
import Carousel from "react-bootstrap/Carousel";
export default class AdvantureGameSlide extends Component {
    render() {
        return (
            <Carousel className="carousel">
        <Carousel.Item interval={1000}>
          <img
            className="d-block w-100 slideimg"
            src="https://images7.alphacoders.com/952/952280.jpg"
            alt="First slide"
          />
          <Carousel.Caption>
            <h3>Assassin's Creed Odyssey</h3>
            <p>
            Assassin's Creed Odyssey is an action role-playing video game developed by Ubisoft Quebec and published by Ubisoft.
            </p>
          </Carousel.Caption>
        </Carousel.Item>
        <Carousel.Item interval={500}>
          <img
            className="d-block w-100 slideimg"
            src="https://images7.alphacoders.com/715/715957.jpg"
            alt="second slide"
          />
          <Carousel.Caption>
            <h3>Mafia III</h3>
            <p>
            Mafia III is an action-adventure video game developed by Hangar 13 and published by 2K Games. 
            </p>
          </Carousel.Caption>
        </Carousel.Item>
        <Carousel.Item interval={500}>
          <img
            className="d-block w-100 slideimg"
            src="https://images4.alphacoders.com/650/650484.jpg"
            alt="Third slide"
          />
          <Carousel.Caption>
            <h3>Far Cry Primal</h3>
            <p>
            Far Cry Primal is a action-adventure video game developed by Ubisoft Montreal and published by Ubisoft.
            </p>
          </Carousel.Caption>
        </Carousel.Item>
        <Carousel.Item>
          <img
            className="d-block w-100 slideimg"
            src="https://images5.alphacoders.com/821/821221.jpg"
            alt="Forth slide"
          />
          <Carousel.Caption>
            <h3>Death Stranding</h3>
            <p>
            Death Stranding is an action game developed by Kojima Productions. It is the first game from director Hideo Kojima and Kojima Productions after their split from Konami in 2015
            </p>
          </Carousel.Caption>
        </Carousel.Item>
      </Carousel>
        )
    }
}
