import React, { Component } from "react";
import axios from "axios";
import Carousel from "react-bootstrap/Carousel";
import "./style/action.css";

export default class GamesDetails extends Component {
  constructor(props) {
    super(props);

    this.state = {
      gamename: "",
      description: "",
      os: "",
      cpu: "",
      ram: "",
      vcard: "",
      hdd: "",
      language: "",
      imgurl: "",
      downloadurl: "",
    };
  }

  async componentDidMount() {
    await axios
      .get("https://gameweb-react-app.herokuapp.com/actiongames/" + this.props.match.params.id)
      .then((res) => {
        this.setState({
          gamename: res.data.gamename,
          description: res.data.description,
          os: res.data.os,
          cpu: res.data.cpu,
          ram: res.data.ram,
          vcard: res.data.vcard,
          hdd: res.data.hdd,
          language: res.data.language,
          imgurl: res.data.imgurl,
          downloadurl: res.data.downloadurl,
        });
        console.log(res.data);
      })
      .catch(function (err) {
        console.log(err);
      });

      await axios
      .get("https://gameweb-react-app.herokuapp.com/racegames/" + this.props.match.params.id)
      .then((res) => {
        this.setState({
          gamename: res.data.gamename,
          description: res.data.description,
          os: res.data.os,
          cpu: res.data.cpu,
          ram: res.data.ram,
          vcard: res.data.vcard,
          hdd: res.data.hdd,
          language: res.data.language,
          imgurl: res.data.imgurl,
          downloadurl: res.data.downloadurl,
        });
        console.log(res.data);
      })
      .catch(function (err) {
        console.log(err);
      });

      await axios
      .get("https://gameweb-react-app.herokuapp.com/advanturegames/" + this.props.match.params.id)
      .then((res) => {
        this.setState({
          gamename: res.data.gamename,
          description: res.data.description,
          os: res.data.os,
          cpu: res.data.cpu,
          ram: res.data.ram,
          vcard: res.data.vcard,
          hdd: res.data.hdd,
          language: res.data.language,
          imgurl: res.data.imgurl,
          downloadurl: res.data.downloadurl,
        });
        console.log(res.data);
      })
      .catch(function (err) {
        console.log(err);
      });

      await axios
      .get("https://gameweb-react-app.herokuapp.com/sportsgames/" + this.props.match.params.id)
      .then((res) => {
        this.setState({
          gamename: res.data.gamename,
          description: res.data.description,
          os: res.data.os,
          cpu: res.data.cpu,
          ram: res.data.ram,
          vcard: res.data.vcard,
          hdd: res.data.hdd,
          language: res.data.language,
          imgurl: res.data.imgurl,
          downloadurl: res.data.downloadurl,
        });
        console.log(res.data);
      })
      .catch(function (err) {
        console.log(err);
      });

      await axios
      .get("https://gameweb-react-app.herokuapp.com/horrorgames/" + this.props.match.params.id)
      .then((res) => {
        this.setState({
          gamename: res.data.gamename,
          description: res.data.description,
          os: res.data.os,
          cpu: res.data.cpu,
          ram: res.data.ram,
          vcard: res.data.vcard,
          hdd: res.data.hdd,
          language: res.data.language,
          imgurl: res.data.imgurl,
          downloadurl: res.data.downloadurl,
        });
        console.log(res.data);
      })
      .catch(function (err) {
        console.log(err);
      });
  }

  render() {
    return (
      <div>
        <Carousel className="carousel">
          <Carousel.Item interval={1000}>
            <img
              className="d-block w-100 slideimg"
              src={this.state.imgurl}
              alt="First slide"
            />
            <Carousel.Caption>
              <h1>{this.state.gamename}</h1>
            </Carousel.Caption>
          </Carousel.Item>
        </Carousel>
        <div className="bg-dark mainpg">
          <div className="container pt-4">
            <h4 className="text-white">SYSTEM REQUIREMENTS:</h4>
            <br></br>
            <div className="col">
              <h6 className="specdetails">OS: {this.state.os}</h6>
              <h6 className="specdetails">CPU: {this.state.cpu}</h6>
              <h6 className="specdetails">RAM: {this.state.ram}</h6>
              <h6 className="specdetails">Video Card: {this.state.vcard}</h6>
              <h6 className="specdetails">Hard Disk Space: {this.state.hdd}</h6>
              <h6 className="specdetails">Language: {this.state.language}</h6>

            </div>
            <br></br>

            <h4 className="text-white">DESCRIPTION:</h4>
            <div className="pl-3">
              <p className="specdetails">{this.state.description}</p>
            </div>
            <div>
            <a href={this.state.downloadurl} className="btn btn-success download">Download</a>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
