import React, { Component } from "react";
import { Form, Button } from "react-bootstrap";
import axios from 'axios';

export default class Admin extends Component {
  constructor(props) {
    super(props);
    this.onchangegamename = this.onchangegamename.bind(this);
    this.onchangedescription = this.onchangedescription.bind(this);
    this.onchangecpu = this.onchangecpu.bind(this);
    this.onchangegamemode = this.onchangegamemode.bind(this);
    this.onchangeos = this.onchangeos.bind(this);
    this.onchangeram = this.onchangeram.bind(this);
    this.onchangevcard = this.onchangevcard.bind(this);
    this.onchangedownloadurl = this.onchangedownloadurl.bind(this);
    this.onchangeimgurl = this.onchangeimgurl.bind(this);
    this.onchangelanguage = this.onchangelanguage.bind(this);
    this.onchangehdd=this.onchangehdd.bind(this);
    this.onsubmit = this.onsubmit.bind(this);
    this.state = {
      gamename: "",
      description: "",
      os: "",
      cpu: "",
      ram: "",
      vcard: "",
      hdd: "",
      language: "",
      imgurl: "",
      downloadurl: "",
      gamemode: "",
    };
  }

  onchangegamemode(e) {
    this.setState({
      gamemode: e.target.value,
    });
  }

  onchangegamename(e) {
    this.setState({
      gamename: e.target.value,
    });
  }

  onchangedescription(e) {
    this.setState({
      description: e.target.value,
    });
  }
  onchangeos(e) {
    this.setState({
      os: e.target.value,
    });
  }

  onchangecpu(e) {
    this.setState({
      cpu: e.target.value,
    });
  }
  onchangeram(e) {
    this.setState({
      ram: e.target.value,
    });
  }
  onchangevcard(e) {
    this.setState({
      vcard: e.target.value,
    });
  }
  onchangehdd(e) {
    this.setState({
      hdd: e.target.value,
    });
  }
  onchangelanguage(e) {
    this.setState({
      language: e.target.value,
    });
  }
  onchangeimgurl(e) {
    this.setState({
      imgurl: e.target.value,
    });
  }
  onchangedownloadurl(e) {
    this.setState({
      downloadurl: e.target.value,
    });
  }

 async onsubmit(e) {
    e.preventDefault();
    const gamecat = this.state.gamemode;
    const gameadd = {
      gamename: this.state.gamename,
      description: this.state.description,
      os: this.state.os,
      cpu: this.state.cpu,
      ram: this.state.ram,
      vcard: this.state.vcard,
      hdd: this.state.hdd,
      language: this.state.language,
      imgurl: this.state.imgurl,
      downloadurl: this.state.downloadurl,
    };
    if(gamecat === "action"){
        console.log(gameadd);
        await axios.post('https://gameweb-react-app.herokuapp.com/actiongames/add',gameadd)
    .then(res => console.log(res.data));
    }
    else if(gamecat ==="racing"){
        console.log(gameadd);
        await axios.post('https://gameweb-react-app.herokuapp.com/racegames/add',gameadd)
    .then(res => console.log(res.data));
    }
    else if(gamecat ==="sports"){
        console.log(gameadd);
        await axios.post('https://gameweb-react-app.herokuapp.com/sportsgames/add',gameadd)
    .then(res => console.log(res.data));
    }
    else if(gamecat === "advanture"){
        console.log(gameadd);
        await axios.post('https://gameweb-react-app.herokuapp.com/advanturegames/add',gameadd)
    .then(res => console.log(res.data));
    }
    else if(gamecat === "horror"){
        console.log(gameadd);
        await axios.post('https://gameweb-react-app.herokuapp.com/horrorgames/add',gameadd)
    .then(res => console.log(res.data));
    }
    
    this.setState({
        gamename: '',
      description: '',
      os: '',
      cpu: '',
      ram: '',
      vcard: '',
      hdd: '',
      language: '',
      imgurl: '',
      downloadurl: '',
      gamemode: '',
      })
  }

  render() {
    return (
      <div className="container" style={{marginTop:"100px"}}>
        <Form onSubmit={this.onsubmit}>
          <Form.Group controlId="exampleForm.ControlSelect1">
            <Form.Label>Games Categories</Form.Label>
            <Form.Control
              as="select"
              value={this.state.gamemode}
              onChange={this.onchangegamemode}
            >
            <option>Select game type</option>
              <option value="action">Action</option>
              <option value="racing">Racing</option>
              <option value="sports">Sports</option>
              <option value="advanture">Advanture</option>
              <option value="horror">Horror</option>
            </Form.Control>
          </Form.Group>
          <Form.Group>
            <Form.Label>Name</Form.Label>
            <Form.Control
              type="text"
              placeholder="name"
              value={this.state.gamename}
              onChange={this.onchangegamename}
            />
          </Form.Group>

          <Form.Group>
            <Form.Label>Description</Form.Label>
            <Form.Control
              type="text"
              placeholder="Description"
              value={this.state.description}
              onChange={this.onchangedescription}
            />
          </Form.Group>
          <Form.Group>
            <Form.Label>os</Form.Label>
            <Form.Control
              type="text"
              placeholder="operating system"
              value={this.state.os}
              onChange={this.onchangeos}
            />
          </Form.Group>
          <Form.Group>
            <Form.Label>CPU</Form.Label>
            <Form.Control
              type="text"
              placeholder="cpu"
              value={this.state.cpu}
              onChange={this.onchangecpu}
            />
          </Form.Group>
          <Form.Group>
            <Form.Label>RAM</Form.Label>
            <Form.Control
              type="text"
              placeholder="ram"
              value={this.state.ram}
              onChange={this.onchangeram}
            />
          </Form.Group>
          <Form.Group>
            <Form.Label>VCARD</Form.Label>
            <Form.Control
              type="text"
              placeholder="vcard"
              value={this.state.vcard}
              onChange={this.onchangevcard}
            />
          </Form.Group>
          <Form.Group>
            <Form.Label>HDD</Form.Label>
            <Form.Control
              type="text"
              placeholder="hdd"
              value={this.state.hdd}
              onChange={this.onchangehdd}
            />
          </Form.Group>
          <Form.Group>
            <Form.Label>Language</Form.Label>
            <Form.Control
              type="text"
              placeholder="language"
              value={this.state.language}
              onChange={this.onchangelanguage}
            />
          </Form.Group>
          <Form.Group>
            <Form.Label>ImageURL</Form.Label>
            <Form.Control
              type="text"
              placeholder="imgUrl"
              value={this.state.imgurl}
              onChange={this.onchangeimgurl}
            />
          </Form.Group>
          <Form.Group>
            <Form.Label>DownloadURL</Form.Label>
            <Form.Control
              type="text"
              placeholder="downloadURL"
              value={this.state.downloadurl}
              onChange={this.onchangedownloadurl}
            />
          </Form.Group>

          <Button variant="primary" type="submit">
            Add
          </Button>
        </Form>
      </div>
    );
  }
}
