import React, { Component } from "react";
import "./style/action.css";
import Carousel from "react-bootstrap/Carousel";
export default class HorrorGameSlide extends Component {
  render() {
    return (
      <Carousel className="carousel">
        <Carousel.Item interval={1000}>
          <img
            className="d-block w-100 slideimg"
            src="https://images.alphacoders.com/241/241491.jpg"
            alt="First slide"
          />
          <Carousel.Caption>
            <h3>Prototype 2</h3>
            <p>
              Prototype 2 is a 2012 open world action-adventure video game.
              Developed by Canadian studio Radical Entertainment and published
              by Activision
            </p>
          </Carousel.Caption>
        </Carousel.Item>
        <Carousel.Item interval={500}>
          <img
            className="d-block w-100 slideimg"
            src="https://images6.alphacoders.com/888/888183.jpg"
            alt="second slide"
          />
          <Carousel.Caption>
            <h3>Overkill's The Walking Dead</h3>
            <p>
              Overkill's The Walking Dead is a first-person shooter developed by
              Overkill Software and published by Starbreeze Studios and 505
              Games.
            </p>
          </Carousel.Caption>
        </Carousel.Item>
        <Carousel.Item interval={500}>
          <img
            className="d-block w-100 slideimg"
            src="https://images2.alphacoders.com/105/1057560.jpg"
            alt="Third slide"
          />
          <Carousel.Caption>
            <h3>Resident Evil 3</h3>
            <p>
              Resident Evil 3 is a survival horror game developed and published
              by Capcom. It is a remake of Resident Evil 3
            </p>
          </Carousel.Caption>
        </Carousel.Item>
        <Carousel.Item>
          <img
            className="d-block w-100 slideimg"
            src="https://images6.alphacoders.com/984/984056.png"
            alt="Forth slide"
          />
          <Carousel.Caption>
            <h3>Dead Effect 2</h3>
            <p>
              Dead Effect 2 is a 2015 video game developed and published by
              BadFly Interactive. It is a sequel to Dead Effect.
            </p>
          </Carousel.Caption>
        </Carousel.Item>
      </Carousel>
    );
  }
}
